import React, {Component} from 'react';
import Supervisor from './images/icon-supervisor.svg';
import TeamBuilder from './images/icon-team-builder.svg';
import Calculator from './images/icon-calculator.svg';
import Karma from './images/icon-karma.svg';


export default class CardsComponent extends Component {
    render() {
        return (
            <div>
                <header>
                    <h1 id="first">Reliable, efficient delivery</h1>
                    <h1 id="second">Powered by Technology</h1>

                    <p>
                    Our Artificial Intelligence powered tools use millions of project data points 
                    to ensure that your project is successful
                    </p>  
                </header>

                <div className="container">

                    <div className="box box-cyan box-push">
                        <h2>Supervisor</h2>
                        <p>Monitors activity to identify project roadblocks</p>
                        <img src={Supervisor} alt="supervisor"/>
                    </div>

                    <div className="box box-red">
                        <h2>Team Builder</h2>
                        <p>Scans our talent network to create the optimal team for your project</p>
                        <img src={TeamBuilder} alt="team-builder"/>
                    </div>  

                    <div className="box box-blue box-push">
                        <h2>Calculator</h2>
                        <p>Uses data from past projects to provide better delivery estimates</p>
                        <img src={Calculator} alt="calculator"/>
                    </div> 

                    <div className="box box-orange">
                        <h2>Karma</h2>
                        <p>Regularly evaluates our talent to ensure quality</p>
                        <img src={Karma} alt="karma"/>
                    </div>
                </div>
            </div>
        )
    }    
}

